@import url('https://fonts.googleapis.com/css2?family=Libre+Bodoni:ital@1&family=Martel+Sans:wght@200&family=Oswald&display=swap');
@import url('reset.css');
html,body{
    width: 100%;
    background-color: #f0f0f0;
    font-family: 'Oswald', sans-serif;
}
#root{
    display: flex;
    flex-direction: column;
}
.row{
display: flex;
flex-direction: row;
}
.col{
    display: flex;
    flex-direction: column;
}
.select-box{
    margin: 5px auto;
    line-height: 1.1;
}
.container{
    height: auto;
    width: auto;
    max-width: 680px;
    margin: 0 auto ;
    //padding: 10px;
    flex-wrap: wrap;
    
    .item{
        max-width: 320px;
        background: darken($color: #2274A5, $amount: 12);
        box-shadow: darken($color: #2274A5, $amount: 30) -7px 7px 10px;
        border-radius: 12px;
        color: #fff;
        text-align: left;
        &:nth-child(2n+1){
            margin: 10px auto 10px 10px ;
        }
        &:nth-child(2n){
            margin: 10px  10px 10px auto ;
        }
        .title{
            font-family: 'Libre Bodoni', serif;
            line-height: 1.2;
            font-size: 22px;
            font-weight: bold;
            margin: 20px 10px 5px;
            text-shadow: darken($color: #f0f0f0, $amount: 30) -2px 3px 6px;
            
        }
        .thumb{
            margin: 5px 10px;
            position: relative;
            
            img{
                width: 300px;
                height: 200px;
                border-radius: 12px;
                box-shadow: #11384e -3px 3px 4px;
            }
            .source,.date{
                font-family: 'Martel Sans', sans-serif;
                font-size: 12px;
                cursor: default;
                user-select: none;
                position: absolute;
                background: #00000040;
                padding: 3px;
                border-radius: 6px;
                text-align: center;
                
            }
            .source{
                bottom: 3px;
                right: 5px;
            }
            .date{
                top: 3px;
                right: 5px;
            }
            
        }
        .description{
            font-size: 15px;
            line-height: 1.2;
            margin: 5px 10px 20px;
            text-indent: .5rem;
        }
    }
}
@media screen and (max-width:720px) {
    .container{
        max-width: 400px;
        .item{
            
            &:nth-child(2n+1){
                margin: 10px  auto ;
            }
            &:nth-child(2n){
                margin: 10px  auto ;
            }
        }
    }
    
}